import React from 'react'
import ReactDOM from 'react-dom'

const Fruit = (props) => {  
  return (    
    <div>      
      <p>This is an {props.name}</p>    
    </div>  
  )
}

const App = () => {
  const now = new Date()
  return(
    <div>
      <h1>Hello world</h1>
      <Fruit name="apple" />
      <Fruit name="orange"/>
      <Fruit name="interesting website"/>
      <p>It is {now.toString()}</p>
    </div>
  )
}

ReactDOM.render(<App />, document.getElementById('root'))